/* General Styling */

body {
    background: #000046;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #1CB5E0, #000046);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #1CB5E0, #000046); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;
}

.navbar-light .navbar-nav .nav-link {
    color: white;
}

a:hover {
    color: darkblue;
}

a:after {
    color: white;
}

/* End General Styling */

/* Footer Styling */
footer {
    padding: .4em 12em 2em 12em;
    background-color: rgba(0, 0, 70, .1);
    display: flex;
    justify-content: center;
    font-weight: bold;
}

.fa {
    font-size: 1.5em;
}

/* End Footer Styling */

/* Navbar Styling */

.navigationBar {
    background-color: rgba(0, 0, 70, .1);
    font-weight: 700;
}

.tab-bar {
    background-color: white;
    color: darkblue;
    padding-left: 1em;
}

/* End Navbar Styling */

/* Resume Page Styling */

.resume-title, .joey-resume {
    display: flex;
    justify-content: center;
}

/* End Resume Page Styling */

/* Web Development Page Styling */
.card {
    background-color: rgba(0, 0, 100, .1);
}

.budgetImage, .passwordImage {
    width: 45em;
    height: 27em;
    /* margin: 1em; */
    border: .5em solid;
    border-color: rgb(0, 80, 150);
}

.exerciseTrackerImage {
    width: 35em;
    height: 27em; 
    /* margin: 1em; */
    border: .5em solid;
    border-color: rgb(0,80,150);
}

.geolocationImage {
    width: 30em;
    height: 27em;
    /* margin: 1em; */
    border: .5em solid;
    border-color: rgb(0, 80, 150);
}

.allWebImages {
    display: flex;
    justify-content: center;
}

.card-title {
    font-weight: 700;
    display: flex; 
    justify-content: center;
}

.deploy, .githubProject {
    display: inline-block;
    align-items: left;
    background-color: rgb(28, 181, 224);
}

.deploy:hover, .githubProject:hover {
    background-color: transparent;
}

.description {
    margin-left: 2em;
}

.description, .buttons, .technologies {
    display: flex;
    justify-content: center;
}

/* End Web Development Page Styling */

/* Social Media Page Styling */

.worldTurtleDay, .dealOfTheWeek, .firstDayFall, .christmasEve {
    border: .5em solid;
    border-color: rgb(0, 80, 150);
    width: 50%;
}

.rugCatalog, .textileCatalog {
    width: 50%;
    display: flex;
    justify-content: center;
}

.rugCatalogRow, .textileCatalogRow {
    display: flex;
    justify-content: center;
    margin-bottom: 1em;
}

.allSocialMediaImages {
    display: flex;
    justify-content: center;
    margin-bottom: 1em;
}

/* End Social Media Page Styling */

/* Home Page Styling */

.joey {
    border: .5em solid;
    border-color: rgb(0, 80, 150);
}


/* End Home Page Styling */